<template>
    <section class="bg-white text-white">
         <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure>

         <div class="bg-blue-180 pb-12">
            <h2 class="text-3xl py-6 text-white 
            md:text-6xl
            lg:text-4xl">- Crear nueva cuenta -</h2>            

            <div class="container border-dashed border-2 rounded border-white p-5">
              <form class="flex gap-x-3 gap-y-6 flex-wrap shadow-round bg-darkBlue text-xl px-10 py-8">        

                <h3 class="w-full text-left border-b font-bold text-2xl pb-2">Informacion Personal</h3>
<!-- Informacion Personal -->
                <div class="flex flex-wrap gap-y-6 gap-x-3 w-full">
                  <input name="first_name" 
                    type="text" 
                    placeholder="Nombre *"
                    required
                    v-model="first_name" 
                    class="w-5/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">                 

                  <input name="email" 
                    type="email" 
                    placeholder="E-Mail *" 
                    required
                    v-model="email" 
                    class="w-3/11 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                  <input name="last_name" 
                    type="text" 
                    placeholder="Apellido *"
                    required
                    v-model="last_name" 
                    class="w-5/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">                                     

                  <input name="phone" 
                    type="text" 
                    placeholder="Teléfono" 
                    v-model="phone" 
                    class="w-3/11 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                    <div class="flex flex-col items-start w-full">       
                      <h4 class="w-full text-left pb-3">Genero *</h4>            
                      <div class="flex border rounded-4xl items-start gap-x-6 p-3">
                        <label>
                          <input name="gender" type="radio" value="1" v-model="gender">
                          Masculino
                        </label>                    
                        <label>
                          <input name="gender" type="radio" value="2" v-model="gender">
                          Femenino
                        </label>
                      </div>
                  </div>
                </div>

                <h3 class="w-full text-left border-b font-bold text-2xl pb-2">Seguridad</h3>
<!-- Seguridad -->
                <div class="flex gap-x-3 w-full text-left font-bold text-2xl pb-2">
                  <input name="password" 
                    :type="(showpass) ? 'text' : 'password'" 
                    placeholder="Contraseña *"
                    required
                    v-model="password" 
                    class="w-3/10 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">    

                  <input name="repassword" 
                    :type="(showpass) ? 'text' : 'password'" 
                    placeholder="Repetir Contraseña *"
                    required
                    v-model="repassword"                     
                    class="w-3/10 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">                      
                  <div class="flex items-center gap-x-4">                    
                    <button type="button" @click="showpass=!showpass" v-show="!showpass"><i class="fas fa-eye-slash"></i></button>
                    <button type="button" @click="showpass=!showpass" v-show="showpass"><i class="fas fa-eye"></i></button>
                    <small class="text-red-700" v-show="!samepass">Las contraseñas deben ser iguales.</small>
                  </div>
                </div>

                <hr>
                <h3 class="w-full text-left border-b font-bold text-2xl pb-2">Direcciones</h3>
<!-- Direcciones -->
                <div class="flex flex-wrap gap-y-6 gap-x-3 w-full">
                  <model-select :options="countries"
                        v-model="country"
                        placeholder="Seleccione País *"
                        required
                        class="border border-white rounded-4xl text-orange bg-darkBlue font-500 text-xl text-left px-6 py-3"
                        >
                  </model-select>

                  <input name="province" 
                    type="text" 
                    placeholder="Provincia" 
                    v-model="province" 
                    class="w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                  <input name="city" 
                    type="text" 
                    placeholder="Ciudad" 
                    v-model="city" 
                    class="w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">
                  
                  <input name="zip_code" 
                    type="text" 
                    placeholder="Codigo Postal" 
                    v-model="zip_code" 
                    class="w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                  <input name="address" 
                    type="text" 
                    placeholder="Dirección" 
                    v-model="address" 
                    class="w-8/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">
                </div>

                <hr>                
                <h3 class="w-full text-left border-b font-bold text-2xl pb-2">Informacion de Agencia</h3>
                <input id="is_agent" name="is_agent" type="checkbox" v-model="is_agent">
                <label for="is_agent">Agente de viaje</label>
<!-- Informacion de Agencia -->
                <div class="flex gap-3 w-full">
                  <input name="company" 
                    type="text" 
                    placeholder="Compañía" 
                    v-model="company" 
                    class="w-2/7 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                  <input name="iata" 
                    type="text" 
                    placeholder="IATA" 
                    v-model="iata" 
                    class="w-2/7 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">
                </div>

                <div class="flex flex-col text-left w-full py-6">
                  <small>
                    Los campos obligatorios marcados con un (*) deben ser rellenados.
                  </small>
                  <div v-if="error_message" class="flex bg-red-500 rounded-3xl w-full text-left text-4xl p-4 my-3">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">
                      Error enviando informacion. No se pudo crear el nuevo usuario
                      <p>{{error_message_text}}</p>
                    </h5>
                  </div>
                  <div v-if="success_message" class="flex bg-green-500 rounded-3xl w-full text-left text-4xl p-4 my-3">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">La información ha sido enviada satisfactoriamente.</h5>
                  </div>
                </div>
                <!-- <div class="w-full flex gap-3 pt-10" @click="activeTerm = !activeTerm">  
                  <span class="cursor-pointer border rounded-4xl text-orange bg-darkBlue px-2 py-1"
                  :class="(activeTerm) ? 'text-orange' : 'text-darkBlue'">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="self-center" for="term">Term and condition</label>
                </div> -->

                <button class="uppercase bg-orange text-white text-xl rounded-4xl font-bold ml-auto px-12 py-1"
                @click="addClient">Enviar</button>
              </form>
            </div>
         </div>
         
    </section>
</template>
<script>
import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import Authorization from "@/services/user/auth.service";

export default {
    name:'NewClient',
    components: {
      ModelSelect
    },
    data() {
      return {
        error_message_text: '',
        error_message: false,
        success_message: false,
        activeTerm: false,    
        samepass: true,            
        showpass: false,            
        //general info
        first_name: '',
        last_name: '',
        phone: '',
        email: '',
        gender: '',
        password: '',
        repassword: '',
        //location
        address: '',
        city: '',
        province: '',
        zip_code: '',
        //agency
        iata: '',
        company: '',
        is_agent: '',
        countries: [],
        country: {
          value: '',
          text: ''
        },
      }
    },    
    created() {
      this.getCountry()
    },   
    watch: {
      repassword() {
        this.samepass =  (this.repassword == this.password)
        this.samepass = (this.repassword == '' && this.password == '')
      }      
    },
    methods: {
      async getCountry() {
        try {
          const {status, data} = await Authorization.checkLogin('/country')          
          if (status == 200) {
            for(let country of data) {
              this.countries.push({
                value: country.id,
                text: country.alias
              })
            }
          }
        } catch(e) {
          console.log(e.response);
        }
      },
      async addClient(e) {
        e.preventDefault();
        // VALIDATE FORM
        const client_data = {data: {
          first_name: this.first_name,
          last_name: this.last_name,
          address: this.address,
          gender_id: (this.gender == '') ? 0 : parseInt(this.gender),
          city: this.city,
          zip_code: this.zip_code,
          email: this.email,
          phone: this.phone,
          country_id: (this.country.value == '') ? 0 : parseInt(this.country.value),
          province: this.province,
          is_travel_agency: this.is_agent,
          i_a_t_a: this.iata,
          company: this.company,
        }}

        if (client_data.data.is_agent === '') client_data.data.is_agent = false 
        if (client_data.data.gender === 0 || client_data.data.first_name === '' 
        || client_data.data.last_name === '' || client_data.data.country_id === 0 
        || client_data.data.email === '') {
            this.success_message = false
            this.error_message = true
        } else {
          try {
            const {status, data} = await Authorization.checkLogin('/clients/create', client_data, 'post')          
            if (status == 200) {
              this.error_message = false
              this.success_message = true
            }
          } catch(e) {
            console.log(e.response);
            this.success_message = false
            this.error_message = true
          }
        }  
        
      },
      comparePass() {
        this.samepass = (this.password != this.repassword)
      }   
    }
}
</script>
<style scoped>
  .ui.dropdown {
    width: 33% !important;
  } 
  input[name="password"] {
    -webkit-text-security:disc
  }
</style>